<template>
    <div class="background">
        <div class="landing-bg-holder">
            <div class="landing-bg-container" :style="sliderStyle">
                <div v-for="(data, index) in doubledSlides" :key="index"
                    :style="{ backgroundImage: `url(${getBackgroundUrl(data.image)})` }" class="landing-bg"></div>
            </div>
        </div>
        <div class="content-holder">
            <div class="content">
                <p class="title">{{ currentSlideData.title }}</p>
                <p class="detail">{{ currentSlideData.detail }}</p>
                <LinkButton title="DETAYLARI GÖR" :linkTo="currentSlideData.link" />
            </div>
            <div class="btns-control-holder">
                <div class="btn-control" @click="prevSlide">
                    <div class="icon left-icon"></div>
                </div>
                <div class="btn-control" @click="nextSlide">
                    <div class="icon right-icon"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import LinkButton from '@/components/LinkButton.vue';
export default {
    name: 'BackgroundSlider',
    components: {
        LinkButton
    },
    computed: {
        filteredSlides() {
            if (!this.landingBgDataArray || this.landingBgDataArray.length === 0) {
                return []; // Return an empty array if landingBgDataArray is not available
            }
            const slide = this.landingBgDataArray[this.currentSlide];
            return slide ? [slide] : []; // Ensure that we return valid data
        },
        sliderStyle() {
            return {
                transform: `translateX(-${this.currentSlide * 100}vw)`,
                transition: this.isAnimating ? 'transform 1s ease-in-out' : 'none'
            };
        },
        doubledSlides() {
            return [...this.landingBgDataArray, this.landingBgDataArray[0]]; // Duplicate last slide
        },
        currentSlideData() {
            const realIndex = this.currentSlide % this.landingBgDataArray.length;
            return this.landingBgDataArray[realIndex];
        }
    },
    methods: {
        // ** Slide Show **
        startSlideShow() {
            this.interval = setInterval(() => {
                this.nextSlide();
            }, 5000); // Automatically change slide every 5 seconds
        },
        clearInterval() {
            if (this.interval) {
                clearInterval(this.interval); // Clears the timer
                this.interval = null; // Reset the interval reference
            }
        },
        nextSlide() {
            if (this.isTransitioning) return;
            this.isTransitioning = true;

            this.clearInterval(); // Stop the automatic slideshow temporarily

            // Animate background-size from 110% to 100% while the slide is changing
            this.animateBackgroundSize(this.currentSlide);

            setTimeout(() => {
                if (this.currentSlide < this.landingBgDataArray.length - 1) {
                    this.currentSlide++;
                } else {
                    this.currentSlide = 0;
                }

                // Reset the background size of the previous slide and grow the new slide
                this.resetBackgroundSize();
                this.growBackgroundSize(this.currentSlide);

                this.isTransitioning = false; // Allow slide transition after animation
                this.startSlideShow(); // Restart automatic slideshow
            }, 100); // The slide transition duration
        },
        animateBackgroundSize(index) {
            const realIndex = index % this.landingBgDataArray.length;
            const slideElement = this.$el.querySelectorAll('.landing-bg')[realIndex];

            if (slideElement) {
                // Add a 1-second delay before shrinking the background size
                setTimeout(() => {
                    slideElement.style.transition = 'background-size 1s ease-in-out';
                    slideElement.style.backgroundSize = '100%'; // Shrink the background after 1-second delay
                }, 1000); // 1 second delay
            }
        },
        resetBackgroundSize() {
        },
        growBackgroundSize(index) {
            const realIndex = index % this.landingBgDataArray.length;
            const slideElement = this.$el.querySelectorAll('.landing-bg')[realIndex];

            if (slideElement) {
                setTimeout(() => {
                    slideElement.style.transition = 'background-size 5s ease-in-out'; // 5 saniyede yavaş büyüme animasyonu
                    slideElement.style.backgroundSize = '110%'; // 110%'a kadar büyüme
                }, 50); // Küçük bir gecikme, ilk yüklemede çakışmayı önlemek için
            }
        },
        prevSlide() {
            if (this.isTransitioning) return;
            this.isTransitioning = true;

            this.clearInterval(); // Stop the automatic slideshow temporarily

            // Animate background-size from 110% to 100% while the slide is changing
            this.animateBackgroundSize(this.currentSlide);

            setTimeout(() => {
                if (this.currentSlide > 0) {
                    this.currentSlide--;
                } else {
                    this.currentSlide = this.landingBgDataArray.length - 1;
                }

                // Reset the background size of the previous slide and grow the new slide
                this.resetBackgroundSize();
                this.growBackgroundSize(this.currentSlide);

                this.isTransitioning = false; // Allow slide transition after animation
                this.startSlideShow(); // Restart automatic slideshow
            }, 100); // The slide transition duration
        },
        getBackgroundUrl(image) {
            return require(`@/assets/images/landing-bg/${image}`);
        },
    },
    data() {
        return {
            currentSlide: 0,
            isAnimating: true,
            isTransitioning: false, // Prevents rapid button clicks
            landingBgDataArray: [
                {
                    title: "SU TEKNOLOJİLERİ",
                    detail: "Ar-Ge çalışmalarımız ile denizcilik sektöründeki hedeflerimizi inceleyin.",
                    link: "/marine",
                    image: "marine.jpg"
                },
                {
                    title: "SAVUNMA SANAYİ",
                    detail: "Zırhlı araçlar için bakım, güvenlik ve otomasyon çalışmalarımızı keşfedin.",
                    link: "/defense",
                    image: "defense.jpg"
                },
                {
                    title: "ENERJİ",
                    detail: "Kalite odaklı yakıt istasyonlarımızda, güvenilir ve yüksek standarttaki hizmetlerimizi deneyimleyin.",
                    link: "/energy",
                    image: "energy.jpg"
                },
                {
                    title: "ENERJİ",
                    detail: "Kalite odaklı yakıt istasyonlarımızda, güvenilir ve yüksek standarttaki hizmetlerimizi deneyimleyin.",
                    link: "/energy",
                    image: "energy.jpg"
                }
            ],
            interval: null
        };
    },
    mounted() {
        // Başlangıçta animasyonu başlat
        this.growBackgroundSize(this.currentSlide);
        // Slideshow'u başlat
        this.startSlideShow();
    },
    beforeUnmount() {
        clearInterval(this.interval); // Clear interval when the component is destroyed
    }
}
</script>

<style>
@import '@/components/BackgroundSlider.css';
</style>